import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from 'app/store';

import { IBoss } from 'types';

interface AppState {
  bossList: IBoss[];
}

const initialState: AppState = {
  bossList: [],
};

export const bossSlice = createSlice({
  name: 'boss',
  initialState,
  reducers: {
    setBossList: (state, { payload: bossList }: PayloadAction<IBoss[]>) => {
      state.bossList = bossList;
    },
  },
});

export const { setBossList } = bossSlice.actions;

export const get = {
  bossList: (state: RootState) => state.boss.bossList,
};

export default bossSlice.reducer;
