import {
  updateInterval$,
  updateTime$,
  announcement$,
} from 'handlers/subscription';
import { useState } from 'react';
import { useEffect } from 'react';
import { IBoss, IStatus } from 'types';
import { getSpawnTime } from 'utils';

const MINUTE_1_MS = 60 * 1000; // in milliseconds
const HOUR_1 = 60; // 1 hour

export default (boss: IBoss, serverTime: Date) => {
  const [hour, setHour] = useState(1);
  const [minute, setMinute] = useState(2);
  const [status, setStatus] = useState<IStatus>(IStatus.NORMAL);

  useEffect(() => {
    const respawnTime = getSpawnTime({
      respawn_time: boss.respawn_time,
      last_killed: boss.last_killed,
    });

    const interval$ = updateInterval$.subscribe((newTime) => {
      const diff = respawnTime.getTime() - newTime.getTime();

      if (diff >= MINUTE_1_MS) {
        const totalMinutes = Math.floor(diff / MINUTE_1_MS);

        if (totalMinutes >= HOUR_1) {
          const remainingMinutes = Math.floor(totalMinutes % HOUR_1);
          const remainingHours = Math.floor(totalMinutes / HOUR_1);

          setHour(remainingHours);
          setMinute(remainingMinutes);
          setStatus(IStatus.NORMAL);

          return;
        }

        setHour(0);
        setMinute(Math.floor(totalMinutes));

        if (totalMinutes <= 5) {
          setStatus(IStatus.BE_READY);
          return;
        }

        setStatus(IStatus.NORMAL);
        return;
      }

      setHour(0);
      setMinute(0);
      setStatus(IStatus.ACTIVE);
      return;
    });

    updateTime$.next();

    return () => interval$.unsubscribe();
  }, [boss.name, boss.last_killed, boss.respawn_time, serverTime]);

  return { hour, minute, status };
};
