declare global {
  interface Window {
    test: any;
    FB: {
      init: Function;
    };
  }
}

export type userId = string;

export interface UserInfo {
  name: string;
  type: UserType;
}

export enum UserType {
  ADMIN = 'admin',
  USER = 'user',
}

export enum IMaps {
  SG_CAMPUS = 'SG Campus',
  MP_CAMPUS = 'MP Campus',
  PNX_CAMPUS = 'PNX Campus',
  SG_HOLE = 'SG Hole',
  MP_HOLE = 'MP Hole',
  PNX_HOLE = 'PNX Hole',
  SHIBUYA = 'Shibuya',
  PY = 'Practicing Yard',
}
export interface IBoss {
  id: string;
  name: string;
  map: IMaps;
  desciption?: string;
  respawn_time: string;
  last_killed: Date;
}
export interface IBossJSON {
  id: string;
  name: string;
  map: string;
  desciption?: string;
  respawn_time: string;
  last_killed: number;
}

export interface IBossTimed extends IBoss {
  hour: number;
  minute: number;
}

export enum IStatus {
  NORMAL = 'normal',
  BE_READY = 'be_ready',
  ACTIVE = 'active',
}
