import { IBoss, IBossJSON } from 'types';
import bossListConst from 'data/boss';

export const date = new Date();

const getInitialServerTime = (date: Date) => {
  const d = date.toISOString();

  return d.slice(0, d.lastIndexOf(':'));
};

export const format2digits = (time: number) => `00${time}`.slice(-2);

const getInitialTime = (date: Date) =>
  `${format2digits(date.getHours())}:${format2digits(date.getMinutes())}`;

export const initialServerTime = date;
export const initialTime = getInitialTime(date);

const addHours = (date: Date, hours: number) =>
  new Date(date.getTime() + hours * 60 * 60 * 1000);

const addMinutes = (date: Date, mins: number) =>
  new Date(date.getTime() + mins * 60 * 1000);

const extraceTime = (time: string): [number, number] => {
  const [hours, minutes] = time.split(':');

  return [Number(hours), Number(minutes)];
};

export const convertDateToStringTime = (date: Date) =>
  `${format2digits(date.getHours())}:${format2digits(date.getMinutes())}`;

const addTime = (date: Date, respawn_time: string) => {
  const [hours, minutes] = extraceTime(respawn_time);

  return new Date(addMinutes(addHours(date, hours), minutes));
};

