export const getUpdatedHourMinute = (
  next_spawn_time: Date,
  serverTime: Date
) => {
  const diff =
    Math.abs(next_spawn_time.getTime() - serverTime.getTime()) / 36e5;

  const hour = Math.floor(diff);

  const minute = Math.floor((diff - hour) * 60);

  return [hour, minute ? minute - 1 : minute];
};

interface ISpawnTime extends Partial<IBoss> {
  respawn_time: string;
  last_killed: Date;
}

export const getSpawnTime = ({ respawn_time, last_killed }: ISpawnTime) => {
  const [hr, min] = respawn_time.split(':').map(Number);

  const addHr = hr * 60 * 60 * 1000;
  const addMin = min * 60 * 1000;

  return new Date(last_killed.getTime() + (addHr + addMin));
};

export const sortBoss = (bossList: IBoss[]) => {
  return bossList.slice(0).sort((bossA, bossB) => {
    const aDate = getSpawnTime(bossA).getTime();
    const bDate = getSpawnTime(bossB).getTime();

    return aDate - bDate;
  });
};

export const cleanString = (str: string) =>
  str.toLowerCase().replace(/\W/g, '');

export const copyObject = (anyObject: Object | []) =>
  JSON.parse(JSON.stringify(anyObject));

export const exportData = (bossListArray: IBoss[], serverTime: Date) => {
  const bossList = (copyObject(bossListArray) as IBossJSON[]).map((b) => {
    b.last_killed = new Date(b.last_killed).getTime();

    return b;
  });

  navigator.clipboard.writeText(
    JSON.stringify({
      serverTime: serverTime.getTime(),
      bossList,
    })
  );
};

export const parseApiSavedData = (json: string) => {
  try {
    const config = JSON.parse(json);

    return {
      serverTime: new Date(config.serverTime),
      bossList: config.bossList.map((boss: IBoss) => {
        boss.last_killed = new Date(boss.last_killed);
        return boss;
      }),
    };
  } catch (e) {
    console.warn('error', e);
    return {
      serverTime: date,
      bossList: bossListConst,
    };
  }
};

export const getLocalConfigData = () => {
  const config = window.localStorage.getItem('config') || '';
  try {
    const { serverTime, bossList } = JSON.parse(config);

    return {
      serverTime,
      bossList,
    };
  } catch (e) {
    return {
      serverTime: date,
      bossList: [],
    };
  }
};
