import React, { useCallback, useEffect, useState } from 'react';
import Main from 'containers/main';
import readFetch from 'hooks/readFetch';

import DateFnsUtils from '@date-io/date-fns';

import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import {
  copyObject,
  exportData,
  getLocalConfigData,
  initialServerTime,
  parseApiSavedData,
} from 'utils';
import { noop } from 'lodash';
import { IBoss, IBossJSON } from 'types';
import produce from 'immer';
import { IFetch } from 'hooks/useFetch';
import { saveToLocalStorage$ } from 'handlers/subscription';

export type IEventCallbackVoid = (event: any) => void;
interface IAppContext {
  serverTime: Date;
  setServerTime: Function;
  updateKillTime: Function;
  onExport: Function;
  onResetAllTime: Function;
}

export const AppContext = React.createContext<IAppContext>({
  serverTime: initialServerTime,
  setServerTime: noop,
  updateKillTime: noop,
  onExport: noop,
  onResetAllTime: noop,
});

interface IAppArgs {
  fetchBossList: IFetch;
}

const App: React.FC<IAppArgs> = ({ fetchBossList }) => {
  const [serverTime, setServerTime] = useState(initialServerTime);

  const [bossList, setBossList] = useState<IBoss[]>([]);

  const updateKillTime = useCallback((id, date) => {
    setBossList(
      produce((list: IBoss[]) => {
        const boss = list.find((boss) => boss.id === id);

        if (boss) {
          boss.last_killed = new Date(date);
        }
      })
    );
  }, []);

  const onExport = useCallback(() => {
    exportData(bossList, serverTime);
  }, [bossList, serverTime]);

  const onResetAllTime = useCallback(() => {
    setBossList(
      produce((list: IBoss[]) => {
        list.map((boss) => {
          boss.last_killed = serverTime;

          return boss;
        });
      })
    );
  }, [serverTime]);

  useEffect(() => {
    const boss = fetchBossList.result;

    if (boss && Array.isArray(boss) && boss.length) {
      const config = parseApiSavedData(boss[0].config);

      const localConfig = getLocalConfigData();

      console.warn('config', config.serverTime, config);

      const serverTime = config.serverTime;

      const bossList = config.bossList.map((boss: IBoss) => {
        const localData: IBoss = localConfig.bossList.find(
          ({ id }: IBoss) => id === boss.id
        );

        if (localData) {
          const dbTime = new Date(boss.last_killed);
          const lsTime = new Date(localData.last_killed);

          if (dbTime > lsTime) {
            boss.last_killed = dbTime;
          } else {
            boss.last_killed = lsTime;
          }
        }

        return boss;
      });

      setServerTime(serverTime);
      setBossList(bossList);
    }
  }, [fetchBossList.result]);

  useEffect(() => {
    if (serverTime && bossList.length) {
      saveToLocalStorage$.next({
        serverTime: new Date(serverTime).getTime(),
        bossList: copyObject(bossList).map((boss: IBossJSON) => {
          boss.last_killed = new Date(boss.last_killed).getTime();

          return boss;
        }),
      });
    }
  }, [serverTime, bossList]);

  return (
    <AppContext.Provider
      value={{
        serverTime,
        setServerTime,
        updateKillTime,
        onExport,
        onResetAllTime,
      }}
    >
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Main bossList={bossList} />
      </MuiPickersUtilsProvider>
    </AppContext.Provider>
  );
};

export default App;
