import React, { useCallback, useContext } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import { KeyboardTimePicker } from '@material-ui/pickers';

import { IBoss } from 'types';
import { cx } from 'emotion';

import { AppContext } from 'App';
import useGetTime from 'hooks/useGetTime';
import { format2digits } from 'utils';

const useStyles = makeStyles((theme) => ({
  container: {
    minWidth: '400px',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 130,
  },
}));

interface IProps {
  boss: IBoss;
}

export default function SelectedBoss({ boss }: IProps) {
  const classes = useStyles();

  const appContext = useContext(AppContext);

  const { hour, minute } = useGetTime(boss, appContext.serverTime);

  const onChangeDate = useCallback(
    (date) => {
      appContext.updateKillTime(boss.id, date);
    },
    [boss.id, appContext]
  );

  const onKilled = useCallback(() => {
    appContext.updateKillTime(boss.id, new Date());
  }, [appContext, boss.id]);

  return (
    <div
      className={cx(
        classes.container,
        'selected-record flex flex-row shadow-lg py-4 px-6 rounded-md'
      )}
    >
      <ul className="w-full">
        <li className="text-xl">{boss.name}</li>
        <li className="text-xs text-gray-600">
          Respawn Time: {boss.respawn_time}
        </li>
        <li className="text-xs text-gray-600">{boss.map}</li>
        <li className="mt-4">
          <KeyboardTimePicker
            label="Last Killed"
            mask="__:__ _M"
            value={boss.last_killed}
            onChange={onChangeDate}
            className={classes.textField}
          />
        </li>
      </ul>
      <div className="right flex flex-col ml-8">
        <div className="timer flex flex-row text-center mb-8">
          <ul className="pr-2">
            <li className="text-3xl">{format2digits(hour)}</li>
            <li className="text-xs text-gray-600">hour</li>
          </ul>
          <ul>
            <li className="text-3xl">{format2digits(minute)}</li>
            <li className="text-xs text-gray-600">minute</li>
          </ul>
        </div>

        <Button variant="contained" color="secondary" onClick={onKilled}>
          Killed
        </Button>
      </div>
    </div>
  );
}
