import React, { useContext } from 'react';

import { format2digits } from 'utils';
import { IBoss } from 'types';

import useGetTime from 'hooks/useGetTime';

import { AppContext } from 'App';

const Boss = (boss: IBoss) => {
  const appContext = useContext(AppContext);

  const { hour, minute, status } = useGetTime(boss, appContext.serverTime);

  return (
    <div
      className={`selected-record flex flex-row shadow-sm py-4 px-6 rounded-md ${status}`}
    >
      <ul className="mr-8  w-full">
        <li className="text-sm">{boss.name}</li>
        <li className="text-xs text-gray-600">{boss.map}</li>
      </ul>
      <div className="right flex flex-col ml-4">
        <div className="timer flex flex-row text-center">
          <ul className="pr-2">
            <li className="text-xl">{format2digits(hour)}</li>
            <li className="text-xs text-gray-600">hour</li>
          </ul>
          <ul>
            <li className="text-xl">{format2digits(minute)}</li>
            <li className="text-xs text-gray-600">minute</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Boss;
