import { empty, interval, Subject } from 'rxjs';
import {
  concatMap,
  distinctUntilChanged,
  map,
  throttleTime,
} from 'rxjs/operators';
import { IBoss } from 'types';

const SECONDS_5 = 5 * 1000;

export const updateTime$ = new Subject();

export const updateInterval$ = updateTime$.pipe(
  throttleTime(SECONDS_5),
  map(() => new Date())
);

interval(SECONDS_5).subscribe(() => updateTime$.next());

export const saveToLocalStorage$ = new Subject();

interface ISaveLocalStorage {
  serverTime: Date;
  bossList: IBoss[];
}
saveToLocalStorage$
  .pipe(
    map((data) => JSON.stringify(data as ISaveLocalStorage)),
    distinctUntilChanged(),
    throttleTime(SECONDS_5)
  )
  .subscribe((data) => {
    window.localStorage.setItem('config', data);
  });

export const announcement$ = new Subject();

interface IBossAnnouncement {
  time: number;
  boss: IBoss;
}

announcement$
  .pipe(
    map((data) => data as IBossAnnouncement),
    concatMap(({ boss, time }: IBossAnnouncement) => {
      const msg = new SpeechSynthesisUtterance(
        `${boss.name} will spawn at ${boss.map} after ${time} minutes`
      );
      window.speechSynthesis.speak(msg);

      return empty();
    })
  )
  .subscribe(() => {});
