import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';

import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import { DateTimePicker } from '@material-ui/pickers';

import { initialServerTime, sortBoss } from 'utils';

import SelectedBoss from 'components/selected';
import Boss from 'components/boss';
import { IBoss, IMaps } from 'types';

import { css, cx } from 'emotion';

import { AppContext } from 'App';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(0),
    marginRight: theme.spacing(0),
    width: 300,
    textAlign: 'center',
  },
}));

interface IMainArgs {
  bossList: IBoss[];
}

const defaultBoss: IBoss = {
  id: 'default',
  name: 'Unknown',
  map: IMaps.SHIBUYA,
  respawn_time: '0:00',
  last_killed: initialServerTime,
};

export default function Main({ bossList }: IMainArgs) {
  const classes = useStyles();
  const [selectedBossId, setSelectedBossId] = useState(defaultBoss.id);

  const appContext = useContext(AppContext);

  const onReset = useCallback(() => {
    appContext.onResetAllTime();
  }, [appContext]);

  const selectedBoss = useMemo(() => {
    const boss = bossList.find((boss) => boss.id === selectedBossId);
    if (boss) {
      return boss;
    }

    return defaultBoss;
  }, [bossList, selectedBossId]);

  const changeServerTime = useCallback(
    (date) => {
      if (date) {
        appContext.setServerTime(new Date(date));
      }
    },
    [appContext]
  );

  useEffect(() => {
    if (bossList.length && selectedBossId === defaultBoss.id) {
      setSelectedBossId(bossList[0].id);
    }
  }, [bossList, selectedBossId]);

  return (
    <div className="flex flex-col mt-2 ml-2">
      <DateTimePicker
        label="Server Up Time"
        inputVariant="outlined"
        value={appContext.serverTime}
        onChange={changeServerTime}
        className={classes.textField}
      />

      <Button
        variant="contained"
        color="primary"
        onClick={onReset}
        className={resetStyle}
      >
        Reset All Time
      </Button>

      <div className="flex flex-row mt-8">
        <div className="flex flex-col mr-4">
          <h1 className="text-sm text-gray-600 mb-4">Selected Record</h1>
          <SelectedBoss boss={selectedBoss} />

          <Button
            variant="contained"
            color="default"
            onClick={() => appContext.onExport()}
            className={resetStyle}
          >
            Copy Local Data
          </Button>

          <p>
            Click "Copy Local Data", go to this{' '}
            <a
              href="https://docs.google.com/spreadsheets/d/1bd_TnHwz14F-0Lq6xnCY70b7VIWudhofzv_aTUsJt2w/edit#gid=0"
              target="_blank"
              rel="noopener noreferrer"
              className={css('color: blue')}
            >
              Google Sheet
            </a>{' '}
            and paste the copied value in A2
          </p>
        </div>
        <div className="list">
          <h1 className="text-sm text-gray-600 mb-4">Boss Spawn List:</h1>
          <ul className="shadow-lg py-4 px-4">
            {sortBoss(bossList).map((boss) => (
              <li
                key={boss.id}
                onClick={() => setSelectedBossId(boss.id)}
                className={cx({ selected: selectedBossId === boss.id })}
              >
                <Boss {...boss} />
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}

const resetStyle = css(`
  width: 150px;
  margin-top: 10px !important;
`);
